.navbar {
    float: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 11vmin;
    margin-left: 20vmin;
}

.logo {
    margin-right: 5vmin;
}

.nav-list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.nav-list-home {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #fff;
}

.nav-list li,
.nav-list-home li {
    list-style: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 2.7vmin;
    text-transform: capitalize;
    padding-left: 9vmin;
    cursor: pointer;
}

.nav-list li a {
    color: #19177E;
}

.nav-list li a:hover,
.nav-list-home li a:hover {
    color: #24a2e9;
}

.navbar_sign {
    position: absolute;
    right: 22vmin;
    top: 12vmin;
    width: 15vmin;
    height: 6vmin;
    border: none;
    border: 0.5vmin solid;
    border-image-source: linear-gradient(89.95deg, rgba(50, 177, 38, 0.8) 1.99%, rgba(36, 162, 233, 0.8) 85.34%, rgba(36, 162, 233, 0.8) 104.58%);
    border-image-slice: 1%;
    background: rgba(236, 236, 236, 0.34);
    backdrop-filter: blur(0.8vmin);

    font-family: var(--font-family-Comfortaa);
    font-size: 2.5vmin;
}

.navbar_sign:hover,
.navbar_home_btn:hover {
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
}

.navbar_home_btn {
    background: #0C0B38;
    position: absolute;
    right: 22vmin;
    top: 12vmin;
    width: 18vmin;
    height: 7vmin;
    border: none;
    border: 0.5vmin solid;
    border-image-source: linear-gradient(89.95deg, rgba(50, 177, 38, 0.8) 1.99%, rgba(36, 162, 233, 0.8) 85.34%, rgba(36, 162, 233, 0.8) 104.58%);
    border-image-slice: 1%;

    color: #CDD5F9;
    font-family:var(--font-family-Comfortaa);
    font-size: 2.5vmin;
}