/* animation-timing-function: cubic-bezier(0.41, 0.28, 0, 0.97); */

@-webkit-keyframes roll-in-top {

  0% {
    -webkit-transform: translateY(-472px) rotate(-540deg);
            transform: translateY(-472px) rotate(-540deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-top {

  0% {
    -webkit-transform: translateY(-472px) rotate(-540deg);
            transform: translateY(-472px) rotate(-540deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@-webkit-keyframes roll-in-down {

  0% {
    -webkit-transform: translateY(472px) rotate(540deg);
            transform: translateY(472px) rotate(540deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-down {

  0% {
    -webkit-transform: translateY(472px) rotate(540deg);
            transform: translateY(472px) rotate(540deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

