
.login_top_text {
    margin-top: 16vmin;
    text-align: center;
    font-family: var(--font-family-Comfortaa);
    font-size: 6.5vmin;
    text-transform: capitalize;
    color: var(--color-text);
}

.email_input:focus,
.password_input:focus {
    border-image-source: linear-gradient(89.95deg, rgba(36, 162, 233, 0.8) 1.99%, rgba(50, 177, 38, 0.8) 104.58%);
    border-image-slice: 1;
}

.input_label_email {
    position: absolute;
    left: 74vmin;
    top: 30.6vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}

.input_label_password {
    position: absolute;
    left: 74vmin;
    top: 47.5vmin;
    font-family: var(--font-family-Urbanist);
    font-size: 3.2vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}

.email_input {
    position: absolute;
    top: 32.2vmin;
    left: 68vmin;
    outline: 0;
    width: 67vmin;
    height: 9.7vmin;
    border: 0.55vmin solid #CECECE;
    border-radius: 3.5vmin;
    font-family: var(--font-family-Urbanist);
}

.password_input {
    position: absolute;
    left: 68vmin;
    top: 49.2vmin;
    outline: 0;
    width: 67vmin;
    height: 9.7vmin;
    border: 0.55vmin solid #CECECE;
    border-radius: 3.5vmin;
    font-family: var(--font-family-Urbanist)
}

small {
    position: absolute;
    left: 109vmin;
    top: 62vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    text-decoration-line: underline;
    text-transform: capitalize;
    color: var(--color-text);
}

.remember_text {
    position: absolute;
    left: 74vmin;
    top: 68vmin;
    cursor: pointer;
    font-family: var(--font-family-Urbanist);
    font-size: 2.9vmin;
    letter-spacing: 0.05vmin;
    color: var(--color-text);
}

.remember_check {
    position: absolute;
    width: 3.4vmin;
    height: 3.4vmin;
    left: 69.2vmin;
    top: 68vmin;
    cursor: pointer;
}

.btn_login {
    left: 94vmin;
    top: 74vmin;
    background: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
    padding: 2.4vmin 4vmin;
    outline: 0;
    position: absolute;
    border-radius: 2.5vmin;
    border: none;
    font-family: var(--font-family-Comfortaa);
    font-size: 3.5vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: #FFFFFF;
}

.btn_login:hover {
    background-color: #32B126;
}

.dont_have_account {
    position: absolute;
    left: 74vmin;
    top: 85vmin;

    font-family: var(--font-family-Comfortaa);
    font-weight: 700;
    font-size: 2.9vmin;
    letter-spacing: 0.01vmin;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--color-text);
}

.for_toggle_btn {
    animation-timing-function: cubic-bezier(0.41, 0.28, 0, 0.97);
    animation-duration: 1000ms;
}